import React from "react";
import { useSmoothScrollToSection } from "../utils/Helpers";

function Lesson9() {
  useSmoothScrollToSection();
  return (
    <div>
      <div className="lesson-header">
        <h2 className="lesson-title background-orange">Lesson 9</h2>
        <h2 className="lesson-title-orange"> (Kabuki)</h2>
      </div>

      <section id="section1">
        <div className="section-header">
          <h3 className="section-circle circle-orange">1</h3>
          <h3 className="section-text-orange">Past Tense Short Forms</h3>
        </div>
        <div className="indent">
          <p style={{ marginBottom: "0px" }}>
            For past tense paradigm of short forms, there are three rules to
            understand:
          </p>
          <ol style={{ marginTop: "5px" }}>
            <li>
              Verbs: Replace the <i>te</i>-form endings  and  with  and
              .
            </li>
            <li>
              -adjectives: Replace the last  with . You may also think
              of this in terms of "dropping  from the long form".
            </li>
            <li>
              -adjectives and nouns: Replace the long form  ending with
              .
              <br />
              Negation:  conjugates as an -adjective. Therefore, for all
              categories, you can replace the last  with .
            </li>
          </ol>
          <table className="table-list table-mini table-border-orange">
            <thead>
              <tr>
                <th colSpan="2" className="table-header text-orange">
                  Past, affirmative
                </th>
                <th className="table-header text-black">compare with:</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td rowSpan="2" style={{ verticalAlign: "top" }}>
                   verbs:
                </td>
                <td>
                  <span className="kanji">
                    <span className="furigana"></span>
                  </span>
                  <span className="underline-orange"></span>
                </td>
                <td>
                  <span className="kanji">
                    <span className="furigana"></span>
                  </span>
                  <span className="underline-orange"></span>
                </td>
              </tr>
              <tr>
                <td>
                  <span className="kanji">
                    <span className="furigana"></span>
                  </span>
                  <span className="underline-orange"></span>
                </td>
                <td>
                  <span className="kanji">
                    <span className="furigana"></span>
                  </span>
                  <span className="underline-orange"></span>
                </td>
              </tr>
              <tr>
                <td> -adjectives:</td>
                <td>
                  <span className="underline-orange"></span>
                </td>
                <td>
                    
                  <span className="underline-orange"></span>
                </td>
              </tr>
              <tr>
                <td> -adjectives:</td>
                <td>
                  <span className="kanji">
                    <span className="furigana"></span>
                  </span>
                  <span className="underline-orange"></span>
                </td>
                <td>
                  <span className="kanji">
                    <span className="furigana"></span>
                  </span>
                  <span className="underline-orange"></span>
                </td>
              </tr>
              <tr>
                <td> noun + :</td>
                <td>
                  <span className="kanji">
                    <span className="furigana"></span>
                  </span>
                  <span className="underline-orange"></span>
                </td>
                <td>
                  <span className="kanji">
                    <span className="furigana"></span>
                  </span>
                  <span className="underline-orange"></span>
                </td>
              </tr>
            </tbody>
            <thead>
              <tr>
                <th colSpan="3" className="table-header text-orange">
                  Past, negative
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td> verbs:</td>
                <td>
                  <span className="kanji">
                    <span className="furigana"></span>
                  </span>
                  <span className="underline-orange"></span>
                </td>
                <td>
                  <span className="kanji">
                    <span className="furigana"></span>
                  </span>
                  <span className="underline-orange"></span>
                </td>
              </tr>
              <tr>
                <td> -adjectives:</td>
                <td>
                  <span className="underline-orange"></span>
                </td>
                <td>
                  
                  <span className="underline-orange"></span>
                </td>
              </tr>
              <tr>
                <td> -adjectives:</td>
                <td>
                  <span className="kanji">
                    <span className="furigana"></span>
                  </span>
                  <span className="underline-orange"></span>
                </td>
                <td>
                  <span className="kanji">
                    <span className="furigana"></span>
                  </span>
                  <span className="underline-orange"></span>
                </td>
              </tr>
              <tr>
                <td> noun + :</td>
                <td>
                  <span className="kanji">
                    <span className="furigana"></span>
                  </span>
                  <span className="underline-orange"></span>
                </td>
                <td>
                  <span className="kanji">
                    <span className="furigana"></span>
                  </span>
                  <span className="underline-orange"></span>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </section>

      <section id="section2">
        <div className="section-header">
          <h3 className="section-circle circle-orange">2</h3>
          <h3 className="section-text-orange">
            Past Tense Short Forms in Informal Speech
          </h3>
        </div>
        <div className="indent">
          <p>
            Short form in the past tense can be used in the same way as the
            present tense forms. Note that the particle  is dropped in
            informal speech.
          </p>
          <div className="tab table-double-column">
            <p>
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
            </p>
            <p>
              <i>Did you have breakfast this morning?</i>
            </p>
          </div>
          <p>
            Unlike  in the present tense,  is not dropped in informal
            speech.
          </p>
          <div className="tab table-double-column">
            <p>
              A:{" "}
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
            </p>
            <p>
              <i>Were you busy last week?</i>
            </p>
          </div>
          <div className="tab table-double-column">
            <p>
              B: 
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
            </p>
            <p>
              <i>No, I wasn't. I had free time.</i>
            </p>
          </div>
        </div>
      </section>

      <section id="section3">
        <div className="section-header">
          <h3 className="section-circle circle-orange">3</h3>
          <h3 className="section-text-orange">
            Past Tense Short Forms in Quoted Speech: 
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
          </h3>
        </div>
        <div className="indent">
          <p>
            Like the present tense, you use the short form in the past tense
            with 
            <span className="kanji">
              <span className="furigana"></span>
            </span>
             (to omoi masu), which means "I think", to report what you
            think took place in the past.
          </p>
          <div className="tab table-double-column">
            <p>
              A: 
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
            </p>
            <p>
              <i>Hey, my pork cutlet is gone!</i>
            </p>
          </div>
          <div className="tab table-double-column">
            <p>
              B: 
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
            </p>
            <p>
              <i>I think Tom ate it.</i>
            </p>
          </div>
          <p className="tab">
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>
              I don't think last winter was very cold. ( = I think last winter
              was not very cold.)
            </i>
          </p>
        </div>
      </section>

      <section id="section4">
        <div className="section-header">
          <h3 className="section-circle circle-orange">4</h3>
          <h3 className="section-text-orange">
            Past Tense Short Forms in Quoted Speech: 
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
          </h3>
        </div>
        <div className="indent">
          <p>
            If you heard somebody utter a sentence in the past tense, you can
            report it with the short form past tense with 
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            .
          </p>
          <p className="tab">
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>Yasmin said that she didn't have lunch.</i>
          </p>
          <p>
            Japanese does not have the "sequence of tense" rule, and the tense
            of the original utterance is preserved when it is reported. If you
            are quoting somebody who used the present tense, you must also use
            the present tense inside the quote.
          </p>
          <p className="tab">
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="underline-orange"></span>
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>
              Yasmin said that she <span className="underline-orange">was</span>{" "}
              having dinner.
            </i>
          </p>
        </div>
      </section>

      <section id="section5">
        <div className="section-header">
          <h3 className="section-circle circle-orange">5</h3>
          <h3 className="section-text-orange">
            Qualifying Nouns with Verbs and Adjectives
          </h3>
        </div>
        <div className="indent">
          <p style={{ marginBottom: "0px" }}>Examples of noun modifications.</p>
          <ol style={{ marginTop: "5px" }}>
            <li>adjectival modification</li>
            <li>phrase describing a person's attribute</li>
            <li>-adjective with a grammatical object</li>
            <li>verb in the short form</li>
          </ol>
          <table className="table-list table-mini table-border-orange">
            <tbody>
              <tr>
                <td>
                  <span className="text-orange">e.g. 1.</span>
                </td>
                <td>
                  <span className="border-orange"></span>
                </td>
                <td>
                  <span className="kanji">
                    <span className="furigana"></span>
                  </span>
                </td>
                <td>
                  <i>
                    a person{" "}
                    <span className="border-orange">who is inteeresting</span>
                  </i>
                </td>
              </tr>
              <tr>
                <td>
                  <span className="text-orange">e.g. 2.</span>
                </td>
                <td>
                  <span className="border-orange"></span>
                </td>
                <td>
                  <span className="kanji">
                    <span className="furigana"></span>
                  </span>
                </td>
                <td>
                  <i>
                    a person{" "}
                    <span className="border-orange">who has long hair</span>
                  </i>
                </td>
              </tr>
              <tr>
                <td>
                  <span className="text-orange">e.g. 3.</span>
                </td>
                <td>
                  <span className="border-orange"></span>
                </td>
                <td>
                  <span className="kanji">
                    <span className="furigana"></span>
                  </span>
                </td>
                <td>
                  <i>
                    a person{" "}
                    <span className="border-orange">who is inteeresting</span>
                  </i>
                </td>
              </tr>
              <tr>
                <td>
                  <span className="text-orange">e.g. 4.</span>
                </td>
                <td>
                  <span className="border-orange"></span>
                </td>
                <td>
                  <span className="kanji">
                    <span className="furigana"></span>
                  </span>
                </td>
                <td>
                  <i>
                    a person{" "}
                    <span className="border-orange">who is interesting</span>
                  </i>
                </td>
              </tr>
              <tr>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td colSpan="2">adjectives and verbs used as qualifiers</td>
                <td>noun</td>
                <td></td>
              </tr>
            </tbody>
          </table>
          <p>
            A verb + , is frequently used as noun modifiers, describing
            people on the basis of their current actions and states.
          </p>
          <div className="tab table-double-column">
            <p>
              A: 
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
            </p>
            <p>
              <i>Which one is Yui?</i>
            </p>
          </div>
          <div className="tab table-double-column">
            <p>
              B:{" "}
              <span className="underline-orange">
                
                <span className="kanji">
                  <span className="furigana"></span>
                </span>
                
                <span className="kanji">
                  <span className="furigana"></span>
                </span>
                
              </span>
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
            </p>
            <p>
              <i>Yui is the one who is reading a book over there.</i>
            </p>
          </div>
          <p className="tab">
            <span className="underline-orange">
              
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
            </span>
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>The student taking pictures over there is Robert.</i>
          </p>
        </div>
      </section>

      <section id="section6">
        <div className="section-header">
          <h3 className="section-circle circle-orange">6</h3>
          <h3 className="section-text-orange">
             and 
          </h3>
        </div>
        <div className="indent">
          <p>
            Use affirmative past tense of a verb   both (i) to talk
            about an event that happened at a certain time in the past ({" "}
            <i>yesterday</i>), and also (ii) to talk about a past event that
            still has an effect at present (used with adverbs like {" "}
            <i>already</i>).
          </p>
          <div className="tab table-double-column">
            <p>
              
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              <span className="underline-orange"></span>
            </p>
            <p>
              <i>I did the homework yesterday.</i>
            </p>
          </div>
          <div className="tab table-double-column">
            <p>
              
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
              <span className="underline-orange"></span>
            </p>
            <p>
              <i>I have already done the homework.</i>
            </p>
          </div>
          <p>
            With the negative, the past is used only to talk about a finished
            time period like , but  is used if your intention is to
            talk about how things stand now ("not yet").
          </p>
          <div className="tab table-double-column">
            <p>
              
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
              <span className="underline-orange"></span>
            </p>
            <p>
              <i>I did not do the homework yesterday.</i>
            </p>
          </div>
          <div className="tab table-double-column">
            <p>
              
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
              <span className="underline-orange"></span>
            </p>
            <p>
              <i>I have not done the homework yet.</i>
            </p>
          </div>
          <table className="table-list table-mini table-border-orange">
            <tbody>
              <tr>
                <td></td>
                <td>
                  <i>have already ...</i>
                </td>
              </tr>
              <tr>
                <td></td>
                <td>
                  <i>have not ... yet</i>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </section>

      <section id="section7">
        <div className="section-header">
          <h3 className="section-circle circle-orange">7</h3>
          <h3 className="section-text-orange">Explanation , Situation</h3>
        </div>
        <div className="indent">
          <p>
             means "because" when added to a sentence. It can also mean a
            sort of explanation when combined with more sentences.
          </p>
          <p>
            If you put two sentences into one with  at the end of one of the
            sentence, the  sentence explains the situation.
          </p>
          <table className="table-list table-mini table-border-orange">
            <tbody>
              <tr>
                <td>
                  <span className="border-orange">explanation</span> 
                  <span className="border-orange">situation</span>
                </td>
              </tr>
              <tr>
                <td style={{ paddingLeft: "50px" }}>
                  = <span className="border-orange">situation</span>,{" "}
                  <i>because</i>{" "}
                  <span className="border-orange">explanation</span>.
                </td>
              </tr>
              <tr>
                <td style={{ paddingLeft: "50px" }}>
                  = <span className="border-orange">explanation</span>;{" "}
                  <i>therefore,</i>
                  <span className="border-orange">situation</span>.
                </td>
              </tr>
            </tbody>
          </table>
          <p className="tab">
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>
              I will study this evening, beacuse we will have an exam tomorrow.
            </i>
            <br />
            <i>
              (= We will have an exam tomorrow; therefore, I will study this
              evening.)
            </i>
          </p>
          <p className="tab">
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>We didn't go out, because it was cold.</i>
            <br />
            <i>(= It was cold; therefore, we didn't go out.)</i>
          </p>
        </div>
      </section>
    </div>
  );
}

export default Lesson9;
