import React from "react";
import { useSmoothScrollToSection } from "../utils/Helpers";

function Lesson6() {
  useSmoothScrollToSection();

  return (
    <div>
      <div className="lesson-header">
        <h2 className="lesson-title background-orange">Lesson 6</h2>
        <h2 className="lesson-title-orange">
          
          <span className="kanji">
            <span className="furigana"></span>
          </span>{" "}
          (A Day in Robert's Life)
        </h2>
      </div>

      <section id="section1">
        <div className="section-header">
          <h3 className="section-circle circle-orange">1</h3>
          <h3 className="section-text-orange">Te-form</h3>
        </div>
        <div className="indent">
          <p>Te-forms are very important. Among its uses:</p>
          <ul className="bullet">
            <li>making requests ("..., please.")</li>
            <li>
              forming a sentence that describes two events or activities. ("I
              did this and did that.")
            </li>
            <li>giving and asking permission ("You may.../May I...?")</li>
            <li>stating that something is foridden ("You must not...")</li>
          </ul>
          <p>
            Te-forms conjugation paradigm is complex. There are separate rules
            for ru-, u-, and irregular verbs. Furthermore, the rule for u-verbs
            is divided into five sub-rules.
          </p>
          <table className="table-expand table-border-orange">
            <tbody>
              <tr>
                <th rowSpan="1" className="table-subheader">
                  Ru-verbs
                </th>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td colSpan="9" className="horizontal-line-wrapper">
                  <div className="horizontal-line-orange"></div>
                </td>
              </tr>
            </tbody>
            <tbody>
              <tr>
                <th rowSpan="14" className="table-subheader">
                  U-verbs with final
                </th>
                <td></td>
                <td rowSpan="3"></td>
                <td rowSpan="3"></td>
                <td rowSpan="3"></td>
                <td></td>
                <td rowSpan="3"></td>
                <td></td>
              </tr>
              <tr>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td colSpan="9" className="horizontal-line-wrapper">
                  <div className="horizontal-line-orange"></div>
                </td>
              </tr>
              <tr>
                <td></td>
                <td rowSpan="3"></td>
                <td rowSpan="3"></td>
                <td rowSpan="3"></td>
                <td></td>
                <td rowSpan="3"></td>
                <td></td>
              </tr>
              <tr>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td colSpan="9" className="horizontal-line-wrapper">
                  <div className="horizontal-line-orange"></div>
                </td>
              </tr>
              <tr>
                <td></td>
                <td></td>
                <td></td>
                <td rowSpan="2"></td>
                <td></td>
                <td rowSpan="2"></td>
                <td></td>
              </tr>
              <tr>
                <td colSpan="3">(Exception)</td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td colSpan="9" className="horizontal-line-wrapper">
                  <div className="horizontal-line-orange"></div>
                </td>
              </tr>
              <tr>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td colSpan="9" className="horizontal-line-wrapper">
                  <div className="horizontal-line-orange"></div>
                </td>
              </tr>
              <tr>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
            </tbody>
            <tr>
              <td colSpan="9" className="horizontal-line-wrapper">
                <div className="horizontal-line-orange"></div>
              </td>
            </tr>
            <tbody>
              <tr>
                <th rowSpan="2" className="table-subheader">
                  Irregular verbs
                </th>
                <td></td>
                <td rowSpan="2"></td>
                <td rowSpan="2"></td>
                <td rowSpan="2"></td>
                <td></td>
                <td rowSpan="2"></td>
                <td></td>
              </tr>
              <tr>
                <td></td>
                <td></td>
                <td></td>
              </tr>
            </tbody>
          </table>
        </div>
      </section>

      <section id="section2">
        <div className="section-header">
          <h3 className="section-circle circle-orange">2</h3>
          <h3 className="section-text-orange"></h3>
        </div>
        <div className="indent">
          <p>
            Use a verbal <i>te</i>-form together with  to make a polite
            request to another person "please do ... for me."
          </p>
          <p className="tab">
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>Please read the textbook.</i>
          </p>
        </div>
      </section>

      <section id="section3">
        <div className="section-header">
          <h3 className="section-circle circle-orange">3</h3>
          <h3 className="section-text-orange">Describing Two Activities</h3>
        </div>
        <div className="indent">
          <p>
            You can use a <i>te</i>-form to combine two or more verbs, as in "I
            did this and then I did that". Note that two verbs cannot be joined
            by , which only connects nouns.
          </p>
          <p className="tab">
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>I will go to the library and check out the books.</i>
          </p>
          <p className="tab">
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>Today I got up at six and studied.</i>
          </p>
          <p>
            The <i>te</i>-form of a verb can also be used to connect a verb more
            "loosely" with the rest of a sentence. I can describe the manner in
            which the action described by the second verb is performed. It also
            can describe the situation for which an apology is made.
          </p>
          <p className="tab">
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>I go to work by bus. (I take a bus to work.)</i>
          </p>
          <p className="tab">
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>
              I am sorry for not bringing in the textbook. (I left the book at
              home, and I am sorry.)
            </i>
          </p>
        </div>
      </section>

      <section id="section4">
        <div className="section-header">
          <h3 className="section-circle circle-orange">4</h3>
          <h3 className="section-text-orange"></h3>
        </div>
        <div className="indent">
          <p>
            A verbal <i>te</i>-form plus  means "you may do...", which
            describes an activity that is permitted. To ask for permission, you
            can turn it into a questino sentence, 
          </p>
          <p>
            The polite and graceful way to grant permission is to say .
          </p>
          <p className="tab">
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>You may use the textbook.</i>
          </p>
          <p className="tab">
            A: 
            <br />
            <i>May I go to the bathroom?</i>
          </p>
          <p className="tab">
            B: / 
            <br />
            <i>You may. / Please.</i>
          </p>
        </div>
      </section>

      <section id="section5">
        <div className="section-header">
          <h3 className="section-circle circle-orange">5</h3>
          <h3 className="section-text-orange"></h3>
        </div>
        <div className="indent">
          <p>
            A verbal <i>te</i>-form plus  means "you must not do
            ...", a strong prohibition statement, as in rules and regulations.
          </p>
          <p className="tab">
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>You must not take pictures here.</i>
          </p>
          <p>
            If somebody asks for permission but you want to deny it, you could
            use  but the setence will come off as harsh unless you
            are in a place of authority. There are softer ways of saying "please
            don't" in lesson 8.
          </p>
        </div>
      </section>

      <section id="section6">
        <div className="section-header">
          <h3 className="section-circle circle-orange">6</h3>
          <h3 className="section-text-orange"></h3>
        </div>
        <div className="indent">
          <p>
            A sentence that ends with  (because) explains the reason or
            cause of a situation.
          </p>
          <table className="table-list table-mini table-border-orange">
            <tbody>
              <tr>
                <td>
                  <span className="text-orange">(situation)</span>{" "}
                  <span className="text-orange">(explanation)</span> 
                </td>
              </tr>
            </tbody>
          </table>
          <p className="tab">
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>
              I will study this evening. (Because) we will have an exam
              tomorrow.
            </i>
          </p>
        </div>
      </section>

      <section id="section7">
        <div className="section-header">
          <h3 className="section-circle circle-orange">7</h3>
          <h3 className="section-text-orange">
             (Offering Assistance)
          </h3>
        </div>
        <div className="indent">
          <p>
            In Lesson 5,  means "Let's ..."  is also used
            in the sense of "let me do ...", as in offering assistance.
          </p>
          <p className="tab">
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>Shall I carry your bag?</i>
          </p>
        </div>
      </section>
    </div>
  );
}

export default Lesson6;
