import React from "react";
import { useSmoothScrollToSection } from "../utils/Helpers";

function Lesson11() {
  useSmoothScrollToSection();

  return (
    <div>
      <div className="lesson-header">
        <h2 className="lesson-title background-orange">Lesson 11</h2>
        <h2 className="lesson-title-orange">
          <span className="kanji">
            <span className="furigana"></span>
          </span>
           (After the Vacation)
        </h2>
      </div>

      <section id="section1">
        <div className="section-header">
          <h3 className="section-circle circle-orange">1</h3>
          <h3 className="section-text-orange"></h3>
        </div>
        <div className="indent">
          <p>
            Use a verb stem (before ) +  to describe hope or
            aspiration.
          </p>
          <p className="tab">
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="underline-orange"></span>
            <br />
            <i>I want to see a film this weekend.</i>
          </p>
          <p className="tab">
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="underline-orange"></span>
            <br />
            <i>I want to go to China someday.</i>
          </p>
          <table className="table-list table-mini table-border-orange">
            <tbody>
              <tr>
                <td>verb stem + </td>
                <td>
                  <i>I want to do ...</i>
                </td>
              </tr>
            </tbody>
          </table>
          <p>
            A verb that usually takes the particle  can either have the
            particle  or  when it is followed by . Particles other than
             remain the same, like .
          </p>
          <p>
             conjugates as an -adjective. Examples of negative and past
            tense:
          </p>
          <p className="tab">
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="underline-orange"></span>
            <br />
            <i>I don't want to see that person.</i>
          </p>
          <p className="tab">
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="underline-orange"></span>
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>
              I went to a convenience store, because I wanted to buy a boxed
              lunch.
            </i>
          </p>
          <p>
            If you wanted something for some time, you can use 
            <span className="kanji">
              <span className="furigana"></span>
            </span>
             instead of .
          </p>
          <p className="tab">
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="underline-orange">
              
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
            </span>
            
            <br />
            <i>I've wanted to study abroad.</i>
          </p>
          <p>
            You do not use  to describe wishes held by others.
            Somebody's else' wants or wishes are usually in quotations,
            observations, or guesses. If you want to say that somebody other
            than you wants to do something, use 
            <span className="kanji">
              <span className="furigana"></span>
            </span>
             with .
          </p>
          <p className="tab">
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="underline-orange">
              
              <span className="kanji">
                <span className="furigana"></span>
              </span>
              
            </span>
            
            <br />
            <i>Mary said she wanted to go to Tibet.</i>
          </p>
          <p>
            To describe your observation that somebody wants to do something,
            you use a special verb  instead of . If a verb takes
            the particle , the derived verb  will retain , unlike
            .
          </p>
          <p className="tab">
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="underline-orange"></span>
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="underline-orange"></span>
            
            <br />
            <i>(It seems) Mary wants to wear a kimono.</i>
          </p>
          <p>
            The verb , which comes from the dictionary form ,
            indicates "I think that she wants to, because of the way she is
            behaving."
          </p>
          <table className="table-list table-mini table-border-orange">
            <tbody>
              <tr>
                <td>
                  <i>I want to ... / Do you want to ... ?</i>
                </td>
                <td>
                  <i>They want to ...</i>
                </td>
              </tr>
              <tr>
                <td> verb stem + </td>
                <td>
                  <i> verb stem + </i>
                </td>
              </tr>
              <tr>
                <td>  conjugates as an -adjective</td>
                <td>
                  <i>  conjugates as an u-verb</i>
                </td>
              </tr>
              <tr>
                <td>  or  for direct object</td>
                <td>
                  <i>  only</i>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </section>

      <section id="section2">
        <div className="section-header">
          <h3 className="section-circle circle-orange">2</h3>
          <h3 className="section-text-orange"></h3>
        </div>
        <div className="indent">
          <p>
            We know that <i>te</i>-form connects two clauses together:
          </p>
          <p className="tab">
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="underline-orange"></span>
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>In Osaka, I will do some shopping and eat dinner.</i>
          </p>
          <p>
            However, this only suggests that shopping and inner are the only
            activities planned and that those two activities will be done in
            that order. To avoid such implications and only want to mention
            activities or events as <i>examples</i>, and in <i>no set order</i>,
            use a special predicate form .
          </p>
          <p className="tab">
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="underline-orange"></span>
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>
              In Osaka, I will{" "}
              <span className="underline-orange">do such things as</span>{" "}
              shopping <span className="underline-orange">and</span> eating
              dinner.
            </i>
          </p>
          <table className="table-list table-mini table-border-orange">
            <tbody>
              <tr>
                <td>verb (short, past) + verb (short, past) + </td>
              </tr>
              <tr>
                <td>
                  <i>do such things as this and that</i>
                </td>
              </tr>
            </tbody>
          </table>
          <p>
            To get  form of a predicate (such as  and ), add
             to the past tense short form of a predicate ( and ).
            The helping verb  at the end of the sentence indicates the tense
            of the sentence.
          </p>
          <p className="tab">
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="underline-orange"></span>
            <br />
            <i>
              I studied and talked with my friends, among other things, over the
              weekend.
            </i>
          </p>
          <p className="tab">
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="underline-orange"></span>
            <br />
            <i>I like dancing, listening to music, and so forth.</i>
          </p>
        </div>
      </section>

      <section id="section3">
        <div className="section-header">
          <h3 className="section-circle circle-orange">3</h3>
          <h3 className="section-text-orange"></h3>
        </div>
        <div className="indent">
          <p>
            The past tense short form of a verb +  describes that you
            did something, or something happened.
          </p>
          <p className="tab">
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="underline-orange"></span>
            <br />
            <i>I have had the experience of climbing Mt. Fuji.</i>
          </p>
          <p className="tab">
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="underline-orange"></span>
            <br />
            <i>Takeshi has never been absent from classes (in his life).</i>
          </p>
          <table className="table-list table-mini table-border-orange">
            <tbody>
              <tr>
                <td>verb (short, past, affirmative) + </td>
                <td>
                  <i>have the experience of ...</i>
                </td>
              </tr>
            </tbody>
          </table>
          <p>
            If someone asks you a question using , you can reply
            / or repeat the whole verbal complex but not
             without a verb.
          </p>
          <p className="tab">
            A: 
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="underline-orange"></span>
            <br />
            <i>Have you ever been to Europe?</i>
          </p>
          <p className="tab">
            B: 
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            &nbsp;&nbsp;&nbsp;&nbsp;
            <br />
            &nbsp;&nbsp;&nbsp;&nbsp;(<span className="text-red"> </span>{" "}
            )
            <br />
            <i>Yes, I have.</i>
          </p>
        </div>
      </section>

      <section id="section4">
        <div className="section-header">
          <h3 className="section-circle circle-orange">4</h3>
          <h3 className="section-text-orange">Noun A  Noun B</h3>
        </div>
        <div className="indent">
          <p>
             connects two nouns like .  suggests that things referred to
            are proposed as examples, much like . Think of  as in etc.
          </p>
          <table className="table-list table-mini table-border-orange">
            <tbody>
              <tr>
                <td>A  B</td>
                <td>
                  <i>A and B, for example</i>
                </td>
              </tr>
            </tbody>
          </table>
          <p className="tab">
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <span className="kanji">
              <span className="furigana"></span>
            </span>
            
            <br />
            <i>
              I went to Kyoto and Nara (for example, and may have visited other
              places as well).
            </i>
          </p>
        </div>
      </section>
    </div>
  );
}

export default Lesson11;
