import React from "react";
import { useSmoothScrollToSection } from "../utils/Helpers";

function Lesson2() {
  useSmoothScrollToSection();

  return (
    <div>
      <div className="lesson-header">
        <h2 className="lesson-title background-orange">Lesson 2</h2>
        <h2 className="lesson-title-orange"> (Shopping)</h2>
      </div>

      <section id="section1">
        <div className="section-header">
          <h3 className="section-circle circle-orange">1</h3>
          <h3 className="section-text-orange"> /  /  / </h3>
        </div>
        <div className="indent">
          <p> = this</p>
          <p> = that</p>
          <p> = that one over there</p>
          <p> = which one</p>
        </div>
      </section>

      <section id="section2">
        <div className="section-header">
          <h3 className="section-circle circle-orange">2</h3>
          <h3 className="section-text-orange">
             /  /  /  + Noun
          </h3>
        </div>
        <div className="indent">
          <p>
            Unlike the previous section, <em>kono</em>, <em>sono</em>,{" "}
            <em>ano</em>, and dono has to be placed before a noun and cannot
            stand alone.
          </p>
          <p> = this thing</p>
          <p> = that thing</p>
          <p> = that thing over there</p>
          <p> = which thing</p>
          <p>
            Can be used in reference to people, unlike <em>kore</em>,{" "}
            <em>sore</em>, and <em>are</em>, which is considered impolite.
          </p>
        </div>
      </section>

      <section id="section3">
        <div className="section-header">
          <h3 className="section-circle circle-orange">3</h3>
          <h3 className="section-text-orange"> /  /  / </h3>
        </div>
        <div className="indent">
          <p>Words for places:</p>
          <p> = here, near me</p>
          <p> = there, near you</p>
          <p> = over there</p>
          <p> = where</p>
        </div>
      </section>

      <section id="section4">
        <div className="section-header">
          <h3 className="section-circle circle-orange">4</h3>
          <h3 className="section-text-orange"> Noun</h3>
        </div>
        <div className="indent">
          <p>How to ask who something belongs to</p>
          <p> = Who</p>
          <p>
             = Whose (with the posessive term <em>no</em>)
          </p>
          <p>Example:</p>
          <p className="tab">
             <span className="underline-orange"></span> {" "}
            <br />
            Kore wa dare no kaban desu ka.
          </p>
        </div>
      </section>

      <section id="section5">
        <div className="section-header">
          <h3 className="section-circle circle-orange">5</h3>
          <h3 className="section-text-orange">Noun </h3>
        </div>
        <div className="indent">
          <p>The particle for the word "too"</p>
          <p>
            In Japanese, you can only put <em>mo</em> directly after the item.
          </p>
          <p className="tab">A  X  (A is X.)</p>
          <p className="tab">
            B <span className="underline-orange"></span> X  (B{" "}
            <span className="underline-orange">too</span> is X.)
          </p>
        </div>
      </section>

      <section id="section6">
        <div className="section-header">
          <h3 className="section-circle circle-orange">6</h3>
          <h3 className="section-text-orange">Noun </h3>
        </div>
        <div className="indent">
          <p>
            To negate a statement, you replace <em>desu</em> with{" "}
            <em>ja nai desu</em>
          </p>
          <p className="tab">A  X  (A is not X.)</p>
          <p>
            <em>Ja nai desu</em> is colloquial (used in ordinary or familiar
            conversations).
          </p>
          <p>
            <strong></strong> is a contraction of <em>de wa</em>, so you
            might find the more formal terms in the following:
          </p>
          <p className="tab">
            A  X <span className="underline-orange"></span>
            (more formal speech style) <br />A  X
            <span className="underline-orange"></span> (formal
            for writing)
          </p>
        </div>
      </section>

      <section id="section7">
        <div className="section-header">
          <h3 className="section-circle circle-orange">7</h3>
          <h3 className="section-text-orange"> / </h3>
        </div>
        <div className="indent">
          <p>
            Statements often end with sentence enders with the tags <em>ne</em>{" "}
            or <em>yo</em>
          </p>
          <p>
            You use <strong></strong> (ne) if you are seeking the listener's
            confirmation or agreement.
          </p>
          <p className="tab">
            A  X <strong></strong>(A is X right?) <br />B  Y 
            <strong></strong>(B is Y isn't it?)
          </p>
          <p>
            If you want to tell the listener that you are confident and the
            listener to believe it, you can use  (yo).
          </p>
          <p className="tab">
            A  X <strong></strong> (Let me assure you. A is
            not X.) <br />B  Y <strong></strong> (B is Y, I tell you.)
          </p>
        </div>
      </section>
    </div>
  );
}

export default Lesson2;
